import { request } from '../../request.js'
import Loading from '../../common/loading/loading.vue'
export default {
	data() {
		return {
			userInfo: '',
			goodsList: [],
			page: 1,
			flag: true,
			$api: this.$api,
			loading: false,
			$src: this.$src
		};
	},
	components:{
		Loading
	},
	onShow() {
		this.getUserInfo();
		this.getGoodsList();
	},
	onReachBottom() {
		if(this.flag) {
			this.page+=1;
			this.getGoodsList()
		}
	},
	methods: {
		// 前往登录页
		goLogin() {
			uni.navigateTo({
				url:"/pages/components/login/login"
			})
		},
		// 获取用户个人信息
		getUserInfo() {
			request({
				url: 'index.php?s=/wap/member/couponList'
			}).then(res => {
				this.userInfo = res.data.member;
			})
		},
		// 获取推荐商品
		getGoodsList() {
			if(this.page != 1) {
				uni.showLoading({
					title:"数据加载中",
					mask:true
				});
			}
			request({
				url: 'index.php?s=/wap/member/couponList',
				method: 'post',
				data: {
					page: this.page
				}
			}).then(res => {
				this.loading = true
				if(res.data.code != 201) {
					
					uni.hideLoading()
					if(res.data.goods && res.data.goods.length < 10) {
						this.flag = false;
					}
					this.goodsList = [...this.goodsList,...res.data.goods];
				}
				
			})
		},
		// 跳转到商品详情页
		goDetail(id) {
			uni.navigateTo({
				url: '/pages/common/goods-detail/goods-detail?id='+id
			})
		}
	},
	
}
